/**
 * @file promo/layout.tsx
 * @description Promo 页面布局 - 通过 CSS 隔离样式
 */

import { ReactNode } from 'react';
import './promo.css';

// 注意：页面标题由 page.tsx 从数据库 page_texts 表动态读取
// 不在此处设置静态 metadata，避免覆盖数据库配置

export default function PromoLayout({
  children,
}: {
  children: ReactNode;
}) {
  return (
    <>
      {/* 预加载字体 */}
      <link rel="preload" href="/promo/fonts/ExtraBold-subset.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />
      <link rel="preload" href="/promo/fonts/SemiBold-subset.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />
      <link rel="preload" href="/promo/fonts/Regular-subset.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />
      <link rel="preload" href="/promo/fonts/Light-subset.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />
      <link rel="preload" href="/promo/fonts/Thin-subset.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />

      {/* Promo 内容 - 根布局已经处理了 body 样式 */}
      {children}
    </>
  );
}
